import WorkflowService from '@/services/commons/WorkflowService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let WorkflowRequestTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultRequestTableFilter: {
      type: String,
      default: null
    },
    defaultRequestTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      requestTableData: [],
      requestTableFilterTimer: null,
      requestTableFilter: this.defaultRequestTableFilter,
      requestTableOrderBy: this.defaultRequestTableOrderBy,
      requestTableContainer: 'loading-container',
      requestTableBusy: false,
      requestTableTotalRows: 0,
      requestTableCurrentPage: 1,
      requestTablePerPage: 100
    }
  },
  computed: {
    requestTableNoDataText: function () {
      return this.bankTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    requestTableTotalPages: function () {
      return Math.ceil(this.requestTableTotalRows / this.requestTablePerPage)
    },
    requestTablePagingInfo: function () {
      return this.pagingInfo(this.requestTableCurrentPage, this.requestTablePerPage, this.requestTableTotalRows)
    }
  },
  methods: {
    loadRequestTableData (loadingContainerId) {
      let self = this
      this.requestTableBusy = true
      this.requestTableContainer = loadingContainerId || this.requestTableContainer
      this.startLoading(this.requestTableContainer)
      WorkflowService.workflowInstances({
        pageIndex: this.requestTableCurrentPage - 1,
        pageSize: this.requestTablePerPage,
        filter: this.requestTableFilter,
        orderByField: this.requestTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.requestTableContainer)
        self.requestTableBusy = false
        self.requestTableTotalRows = response.data.totalRows
        self.requestTableData = response.data.workflowInstanceList || []
      }).catch(function (error) {
        self.stopLoading(self.requestTableContainer)
        self.requestTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshRequestTable () {
      this.loadRequestTableData()
    },
    filterRequestTable (filter) {
      let self = this
      if (this.requestTableFilter !== filter) {
        clearTimeout(this.requestTableFilterTimer)
        this.requestTableFilterTimer = setTimeout(() => {
          self.requestTableFilter = filter
          self.loadRequestTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.requestTableFilterTimer)
      }
    },
    orderRequestTable (orderBy, active) {
      this.requestTableOrderBy = (active ? '' : '-') + orderBy
      this.loadRequestTableData()
    }
  },
  watch: {
    requestTableCurrentPage: function () {
      this.refreshRequestTable()
    },
    requestTablePerPage: function () {
      this.refreshRequestTable()
    }
  }
}

export default WorkflowRequestTableMixin
